import React, {useEffect, useState, createRef} from 'react';
import { connect } from 'react-redux';
import { Divider, Table, Input, Select } from "antd";
import { changLoadingState } from '../../actions/global';
import {RecommendedArticles, QueryComponent} from '../../components';
import { recommendedArticleArr, allBlogLink, blogTypeList } from '../../utils/enums';

const mapState = state => ({
    loading: state.global.isLoading,
});
function Index(props) {
    const [dataSource, setDataSource] = useState(allBlogLink);
    const formRef = createRef();

    const columns = [
        {
            title: '序号',
            dataIndex: 'id',
            key: 'id',
            width: 80,
            fixed: 'left',
        }, {
            title: '文章',
            dataIndex: 'name',
            key: 'name',
            render: (text, item) => <a href={item.link} target="_blank">{text}</a>,
        }, {
            title: '分类',
            dataIndex: 'blogType',
            key: 'blogType',
            render: (val) => {
                const typeArr = val.split(',');
                let text = '';
                const len = typeArr.length;
                // 算法优化，抛去无用的计算
                if ((len === 1 && typeArr[0]) || len > 1) {
                    let count = 0;
                    typeArr.some(item => {
                        blogTypeList.some(ite => {
                            if (ite.id == item) {
                                text += `${ite.name},  `;
                                count += 1;
                            }
                            return (ite.id == item);
                        });
                        return (count >= len);
                    })
                }
                return (
                    <div>{text}</div>
                )
            }
        }
    ];
    useEffect(() => {
        props.changLoadingState();
        return () => props.changLoadingState();
    }, [])

    const extraStyle = {
        width: 200,
        float: 'right',
        margin: '10px',
    };
    return (
        <div>
            <RecommendedArticles
                recommendedArticleArr={recommendedArticleArr}
            />
            <div
                style={{
                    marginTop: 40,
                    background: '#fff',
                    padding: '10px 58px',
                }}
            >
                <Divider orientation="left">最新文章</Divider>
                <QueryComponent
                    wrappedComponentRef={formRef}
                    fields={[
                        {
                            dataIndex: 'name',
                            placeholder: '搜索文章名',
                            styles: {...extraStyle},
                        },
                        {
                            dataIndex: 'blogType',
                            placeholder: '筛选文章分类',
                            styles: {...extraStyle},
                            selectData: blogTypeList,
                        },
                    ]}
                    queryHandle={(queryData) => {
                        const { name, blogType } = queryData;
                        let newDataSource = allBlogLink;
                        name && (newDataSource = newDataSource.filter(item => (item.name).includes(name)));
                        blogType && (newDataSource = newDataSource.filter(ite => (ite.blogType.split(',')).indexOf(`${blogType}`) > -1));
                        setDataSource(newDataSource);
                    }}
                />
                <Table
                    className={'uiTable'}
                    rowKey={'id'}
                    columns={columns}
                    dataSource={dataSource.map((item, index) => ({id: index + 1, ...item}))}
                />
            </div>
        </div>
    );
}

export default connect(mapState, { changLoadingState })(Index);



